@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:400,700&display=swap&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=VT323&display=swap&subset=latin-ext);
@import url(https://fonts.googleapis.com/css?family=Oswald:400,700&display=swap&subset=latin-ext);
html {
  font-family: "Roboto Condensed", sans-serif;
  font-size: 16px; }

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

.header {
  align-items: center;
  background: -webkit-gradient(linear, left top, right top, from(#2a0045), to(#6441a5));
  background: linear-gradient(to right, #2a0045, #6441a5);
  display: flex;
  flex-direction: row;
  height: 7rem;
  margin-bottom: 2rem; }
  @media (max-width: 641px) {
    .header {
      height: 6rem; } }
  .header__img {
    height: 80%;
    margin: 0 0 0 4rem;
    -webkit-transition: 0.3s linear;
    transition: 0.3s linear; }
    @media (max-width: 641px) {
      .header__img {
        margin: 0 0 0 2rem; } }
    .header__img:hover {
      -webkit-animation: shakes 0.2s ease-in-out infinite alternate;
              animation: shakes 0.2s ease-in-out infinite alternate; }
  .header__paragraph {
    color: whitesmoke;
    font-size: 3rem;
    margin: 0 0 0 4rem; }
    @media (max-width: 641px) {
      .header__paragraph {
        font-size: 1.5rem;
        margin: 0 0 0 1rem; } }

.memes {
  padding: 1.3rem;
  text-align: center; }
  .memes__top-text, .memes__bottom-text {
    color: green;
    font-weight: bold;
    font-size: 2rem;
    letter-spacing: 0.2rem;
    text-transform: uppercase; }
  .memes__img {
    height: 22rem;
    width: 32rem; }
    @media (max-width: 641px) {
      .memes__img {
        text-align: center;
        width: 100%; } }
  .memes__top-text {
    left: 50%;
    position: absolute;
    top: 30%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
    @media (max-width: 641px) {
      .memes__top-text {
        top: 40%; } }
  .memes__bottom-text {
    left: 50%;
    position: absolute;
    top: 72%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%); }
    @media (max-width: 641px) {
      .memes__bottom-text {
        top: 80%; } }

.meme-form {
  display: flex;
  justify-content: space-around;
  margin: 0;
  width: 100%; }
  .meme-form__input {
    font-size: 1.5rem;
    height: 2rem;
    text-align: center;
    width: 40%; }
    @media (max-width: 641px) {
      .meme-form__input {
        font-size: 1rem;
        height: 1rem;
        margin: 0.5rem;
        padding: 0.5rem;
        width: 80%; } }
    .meme-form__input:focus {
      background: #d8f3df;
      border: 2px solid green;
      outline: none; }
  .meme-form__button {
    background: -webkit-gradient(linear, left top, right top, from(#2a0045), to(#6441a5));
    background: linear-gradient(to right, #2a0045, #6441a5);
    background-size: 200%;
    border: none;
    border-radius: 2px;
    color: whitesmoke;
    cursor: pointer;
    font-size: 1.5rem;
    letter-spacing: 0.2rem;
    position: relative;
    -webkit-transition: .3s;
    transition: .3s; }
    @media (max-width: 641px) {
      .meme-form__button {
        background-size: 100%;
        font-size: 1rem;
        margin: 0.2rem;
        padding: 0.5rem;
        width: 86%; } }
  .meme-form__button:hover {
    background-position-x: 100%; }
  @media (max-width: 641px) {
    .meme-form {
      align-items: center;
      display: flex;
      flex-direction: column; } }

.footer {
  background: black;
  color: #56e39f;
  font-size: 1.4rem;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  margin: 0;
  padding: 1.4rem;
  text-align: center;
  text-transform: uppercase; }
  @media (max-width: 641px) {
    .footer {
      font-size: 0.8rem;
      padding: 1rem; } }

@-webkit-keyframes shakes {
  0% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg); }
  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg); } }

@keyframes shakes {
  0% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg); }
  100% {
    -webkit-transform: rotate(-10deg);
            transform: rotate(-10deg); } }

